.App {
  height: auto;
  padding: 0 20%;
  padding-top: 60px;
}

.content-wrapper {
    display: flex;
    align-items: center;
    justify-items: center;
  }
  
  .main_content-img{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .main_content-headshot{
    height: 350px;
    width: 350px;
    padding-top: 20px;
    border-radius: 50%;
  }
  
  .main_content-social{
    display: flex;
    justify-content: center;
    margin-top: 5px;
  }
  
  .main_content-personal_information{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 370px;
    width: 300px;
    padding-left: 6%;
  }
  
  .main_content-social a:not(:last-child) {
    margin-right: 20px; /* 设置照片之间的间距 */
  }
  
  .main_content-ul{
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .main_content-li{
    margin-bottom: 10px;
  }

  .main_content-introduce{
    width: 100%;
    margin-left: 10%;
    padding-bottom: 10%;
  }